@import url("https://fonts.googleapis.com/css2?family=Roboto+Flex:opsz,wght@8..144,100;8..144,400;8..144,700&family=Roboto+Mono:wght@100;400;700&family=Roboto+Slab:wght@100;400;700&family=Roboto:wght@100;400;700&display=swap");
*:where(:not(html,
iframe,
canvas,
img,
svg,
video,
audio):not(svg *,
symbol *)) {
  all: unset;
  display: revert;
}
*,
:before,
:after {
  box-sizing: border-box;
}
html {
  -moz-text-size-adjust: none;
  -webkit-text-size-adjust: none;
  text-size-adjust: none;
}
body {
  line-height: 1.5;
}
html,
body {
  height: 100%;
  min-height: 100%;
  overflow: inherit;
}
ol.menu,
ul.menu,
ol.summary,
ul.summary {
  list-style: none;
}
ol[role='list'],
ul[role='list'],
ol.links,
ul.links {
  padding-left: 1em;
}
a:not([class]) {
  text-decoration-skip-ink: auto;
  color: currentColor;
}
a,
button {
  cursor: revert;
}
img,
picture {
  font-style: italic;
  shape-margin: 1rem;
  max-width: 100%;
  max-inline-size: 100%;
  max-block-size: 100%;
  height: auto;
  vertical-align: middle;
}
table {
  border-collapse: collapse;
}
input,
button,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
}
input,
textarea {
  -webkit-user-select: auto;
}
form,
label,
input,
input[type=text],
input[type=email],
input[type=url],
input[type=password],
select,
textarea {
  min-width: 0;
  max-width: 100%;
  resize: vertical;
}
textarea {
  white-space: revert;
}
textarea:not([rows]) {
  min-height: 10em;
}
h1,
h2,
h3,
h4,
button,
input,
label {
  line-height: 1.1;
}
h1,
h2,
h3,
h4,
h6 {
  text-wrap: balance;
}
meter {
  -webkit-appearance: revert;
  appearance: revert;
}
:where(pre) {
  all: revert;
  box-sizing: border-box;
}
::placeholder {
  color: unset;
}
:where([hidden]) {
  display: none;
}
:where([contenteditable]:not([contenteditable="false"])) {
  -moz-user-modify: read-write;
  -webkit-user-modify: read-write;
  overflow-wrap: break-word;
  -webkit-line-break: after-white-space;
  -webkit-user-select: auto;
}
:where([draggable="true"]) {
  -webkit-user-drag: element;
}
:where(dialog:modal) {
  all: revert;
  box-sizing: border-box;
}
::-webkit-details-marker {
  display: none;
}
:target {
  scroll-margin-block: 5ex;
}
@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
  *,
  :before,
  :after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    scroll-behavior: auto !important;
    transition-duration: 0.01ms !important;
  }
}
body.toolbar-horizontal #off-canvas {
  height: calc(100% - 4em);
  min-height: calc(100% - 4em);
}
body #off-canvas {
  height: 100%;
  min-height: 100%;
  grid-template-rows: auto 1fr auto;
}
body #off-canvas * {
  font-family: 'Roboto', sans-serif, ui-sans-serif;
}
body #off-canvas * h1,
body #off-canvas * h2,
body #off-canvas * h3,
body #off-canvas * h4,
body #off-canvas * h5,
body #off-canvas * h6 {
  font-family: 'Roboto Slab', serif, ui-serif;
  font-weight: 700;
  margin: inherit;
}
body #off-canvas * h6 {
  font-size: 1.202rem;
}
body #off-canvas * h5 {
  font-size: 1.444804rem;
}
body #off-canvas * h4 {
  font-size: 1.736654408rem;
}
body #off-canvas * h3 {
  font-size: 2.087458598416rem;
}
body #off-canvas * h2 {
  font-size: 2.509125235296032rem;
}
body #off-canvas * h1 {
  font-size: 3.01596853282583rem;
}
body #off-canvas * h1 * {
  font-size: inherit;
}
.flex {
  display: flex;
}
.flex-row {
  flex-direction: row;
}
.grid {
  display: grid;
}
main > * {
  max-width: calc(100% - 2.5em);
  margin-inline: auto;
}
main figure.hero {
  max-width: 100%;
}
main figure.hero > * {
  max-width: calc(100% - 2.5em);
  margin-inline: auto;
}
main section.content {
  padding-block: 1em;
}
.button {
  background-color: #333;
  border: solid 4px #333;
  color: accent;
  display: flex;
  place-items: center;
}
ul.links {
  margin-block: 1em;
}
.page-title {
  margin-bottom: 0.5rem;
}
.site-branding__logo-image {
  max-width: 5rem;
}
.site-branding__name * {
  font-family: 'Roboto Slab', serif, ui-serif;
}
.copyright-footer {
  margin-bottom: 1.25rem;
}
.block-system-branding-block {
  display: flex;
  align-items: center;
  max-height: 5rem;
  gap: 1.5rem;
}
section.site-content {
  line-height: 1.25;
}
header .site-header__inner {
  display: flex;
  column-gap: 1rem;
  flex-wrap: wrap;
}
header .site-header__inner .site-header__brand-wrapper,
header .site-header__inner .site-header__nav-wrapper {
  display: grid;
}
.with-title .page-title-block {
  margin-bottom: 0.5rem;
}
figure.hero {
  padding-block: 1em;
}
main > figure,
main > section {
  width: auto;
}
.local-tasks-block {
  margin-block: 0.5rem 0.75rem;
}
nav.local-tasks__primary,
nav.local-tasks__secondary {
  display: grid;
}
nav.local-tasks__primary ul {
  display: flex;
  flex-wrap: wrap;
  column-gap: 1.25rem;
  max-width: max-content;
}
nav.local-tasks__primary ul li a {
  display: block;
  padding-block: 0.5rem;
}
.site-header__nav-wrapper .region--navigation nav ul.menu {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(6rem, 1fr));
  grid-auto-flow: column;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li {
  min-width: 6rem;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li a {
  display: grid;
  place-content: center;
  border-top: 4px solid #fff;
  min-height: calc(5rem - 4px);
  transition: border-color 750ms ease-in-out;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li a:hover {
  border-color: #d3d3d3;
}
.site-header__nav-wrapper .region--navigation nav ul.menu li a.is-active {
  border-color: #000;
  color: #000;
}
.region--branding {
  display: grid;
}
.region--navigation {
  display: flex;
  justify-items: end;
}
.page-blog {
  flex-direction: column;
  gap: 2.5rem;
}
body.maintenance-page header img {
  max-height: 55dvh;
}
.page-user.anonymous main section.site-content {
  min-height: 18rem;
  justify-self: center;
  align-self: self-start;
  display: grid;
  grid-gap: 1rem 1.5rem;
  max-width: max-content;
  min-width: 25.5rem;
}
